import axios from 'axios'
// import Store from '../vuex/store'
// qs  post  data 
// axios postContent-type application/json
import qs from 'qs'
axios.defaults.timeout = 5000 // 5s
axios.defaults.baseURL = 'http://ps.iseekids.com:8086'
// eslint-disable-next-line no-tabs
// axios.defaults.withCredentials = true 	//cookiewithCrendentials

// http request  
axios.interceptors.request.use(
  request => {
    request.data = qs.stringify(request.data) // qsdata
    request.headers = {
      'Content-Type': 'application/x-www-form-urlencoded'
      // token:''// headercontent-type
    }
    return request
  },
  error => {
    return Promise.reject(error)
  }
)

/**
 *  get  post 
 *  @param url
 *  @param params
 *  @returns {Promise}
 */

// main.jsVue.use(http) vue
export default {
  install: (Vue) => {
    Vue.prototype.$get = (url, params = {}) => {
      return new Promise((resolve, reject) => {
        axios.get(url, {params})
          .then(response => resolve(response.data))
          .catch(error => reject(error))
      })
    }
    Vue.prototype.$post = (url, params = {}) => {
      return new Promise((resolve, reject) => {
        axios.post(url, params)
          .then(response => resolve(response.data))
          .catch(error => reject(error))
      })
    }
  }
}



